import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from '../../sharedcomponents/loader';
import {AppPage} from '../../sharedcomponents/page';
import {Card} from '../../sharedcomponents/card';
import {Modal, SideModal} from '../../sharedcomponents/popover/modal';
import {AppLabel} from '../../services/i18nservice';

export class HomePage extends React.Component<any, any> {
    constructor(props){
        super(props);
        this.state = {
            showModal: false,
            showSideModal : false
        }
    }

    hideModal(){
        this.setState({ showModal: false });
    }

    hideSideModal(){
        this.setState({ showSideModal:  false });
    }

    render(){
        return <AppPage className="homepage" defaultPadding={ true }>
            <header>
                <AppLabel component="h1" i18n="homepage.title" />
            </header>
            <section>
                <button onClick={ () => this.setState({ showModal : true})}>modal</button>
                <button onClick={ () => this.setState({ showSideModal : true})}>side modal</button>
            </section>
            <Modal show={this.state.showModal} onhide={ this.hideModal.bind(this)}>
                <Card defaultPadding={true}>
                    Hello world, bla bla, bla,<br/>
                    Hello world, bla bla, bla,<br/>
                    Hello world, bla bla, bla,<br/>
                    Hello world, bla bla, bla,<br/>
                    Hello world, bla bla, bla,<br/>
                </Card>
            </Modal>

            <SideModal show={this.state.showSideModal} onhide={ this.hideSideModal.bind(this)}>
                <Card defaultPadding={true}>
                    Hello world
                </Card>
            </SideModal>
        </AppPage>
    }
}


